'use client';

import { useEffect, useState } from 'react';
import { getCertificatesByStudent } from '@/app/actions/certificateActions';

type Certificate = {
  id: string;
  title: string;
  description: string;
  issued_date: string;
  entity_name: string;
  blockchain_tx: string;
};

type Props = {
  studentEmail: string;
};

export default function StudentCertificateList({ studentEmail }: Props) {
  const [certificates, setCertificates] = useState<Certificate[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchCertificates = async () => {
      try {
        const data = await getCertificatesByStudent(studentEmail);
        setCertificates(data);
      } catch {
        setError('Errore durante il recupero dei certificati');
      } finally {
        setLoading(false);
      }
    };

    if (studentEmail) fetchCertificates();
  }, [studentEmail]);

  if (loading) return <p className="text-gray-700">Caricamento certificati...</p>;
  if (error) return <p className="text-red-500">{error}</p>;

  return (
    <section className="mt-8">
      <h2 className="text-2xl font-bold mb-4 text-green-700">I miei certificati</h2>
      {certificates.length === 0 ? (
        <p className="text-gray-500">Nessun certificato disponibile.</p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {certificates.map((cert) => (
            <div key={cert.id} className="bg-white p-4 rounded-xl shadow-md border">
              <h3 className="text-lg font-bold text-green-800">{cert.title}</h3>
              <p className="text-sm text-gray-600">{cert.description}</p>
              <p className="text-sm mt-2">
                <strong>Ente:</strong> {cert.entity_name}
              </p>
              <p className="text-sm">
                <strong>Data:</strong> {new Date(cert.issued_date).toLocaleDateString()}
              </p>
              <a
                href={`https://sepolia.etherscan.io/tx/${cert.blockchain_tx}`}
                target="_blank"
                rel="noopener noreferrer"
                className="text-sm text-blue-600 underline block mt-2"
              >
                Visualizza transazione
              </a>
            </div>
          ))}
        </div>
      )}
    </section>
  );
}
